var FlightBox = React.createClass({
  displayName: 'FlightBox',

  propTypes: {
    flightSchedules: React.PropTypes.array,
    flightDummy: React.PropTypes.object
  },
  getInitialState: function getInitialState() {
    //zeroized the flightSchedules.id if it's null
    var handle = this;
    var newFlightSchedules = this.props.flightSchedules;

    newFlightSchedules = newFlightSchedules.map(function (e, i) {
      e.id = e.id == null ? '' : e.id;
      e.charge_schedule.id = e.charge_schedule.id == null ? '' : e.charge_schedule.id;
      e.name = e.name == null ? "" : e.name;

      return e;
    });

    return {
      flightSchedules: this.props.flightSchedules,
      //teeTimes:this.props.flightSchedules.map( (e,i) => e.flight_matrices.map( (e2,i2) => {return [e2.tee_time, e2.flight_order] }))
      teeTimes: this.props.flightSchedules.map(function (e, i) {
        return e.flight_matrices;
      })
    };
  },
  newSchedule: function newSchedule(e) {
    var newFlightSchedules = this.state.flightSchedules;
    newFlightSchedules.push(this.props.flightDummy);

    var newTeeTimes = this.state.teeTimes;
    newTeeTimes.push(["07:00am", "07:07am"]);

    this.setState({ flightSchedules: newFlightSchedules, teeTimes: newTeeTimes });
  },
  deleteSchedule: function deleteSchedule(e) {
    //delete flight schedules

    //can't delete if there's the last one
    //if(e.target.dataset.value == 0){
    if (this.state.flightSchedules.length == 1) {
      return;
    }

    //figure out why this doesn't work
    var arrayIndex = parseInt(e.target.dataset.value, 10);
    var newFlightSchedules = this.state.flightSchedules;
    var newTeeTimes = this.state.teeTimes;

    newFlightSchedules.splice(arrayIndex, 1);
    newTeeTimes.splice(arrayIndex, 1);

    this.setState({ flightSchedules: newFlightSchedules, teeTimes: newTeeTimes });
  },
  updateFlightInfo: function updateFlightInfo(e) {
    var newFlightSchedules = this.state.flightSchedules;
    if (e.target.dataset.target == "charge_schedule") {
      newFlightSchedules[e.target.dataset.index]["charge_schedule"]['' + e.target.dataset.charge] = e.target.value;
    } else if (e.target.dataset.target == "flight_matrices") {
      console.log("should change flight_matrices");
      var newDayValue = newFlightSchedules[e.target.dataset.index]["flight_matrices"][0]['day' + e.target.dataset.flight];
      newDayValue = newDayValue == 1 ? 0 : 1;
      newFlightSchedules[e.target.dataset.index]["flight_matrices"][0]['day' + e.target.dataset.flight] = newDayValue;
    } else {
      newFlightSchedules[e.target.dataset.index]['' + e.target.dataset.target] = e.target.value;
    }

    //zeroized if insurance_mode is inclusive
    if (newFlightSchedules[e.target.dataset.index]["charge_schedule"]["insurance_mode"] == 2) {
      newFlightSchedules[e.target.dataset.index]["charge_schedule"]["insurance"] = 0;
    }

    this.setState({ flightSchedules: newFlightSchedules });
  },
  addTeeTime: function addTeeTime(e) {
    var newTeeTimes = this.state.teeTimes;
    newTeeTimes[e.target.dataset.indexSchedule].push(e.target.dataset.value);
    newTeeTimes[e.target.dataset.indexSchedule].sort();
    this.setState({ teeTimes: newTeeTimes });
  },
  deleteTeeTime: function deleteTeeTime(e) {
    //don't delete anything if there's on 1 left
    if (this.state.teeTimes[e.target.dataset.indexSchedule].length <= 1) {
      return;
    }

    //delete tee times
    var indexTime = parseInt(e.target.dataset.indexTime);
    var newTeeTimes = this.state.teeTimes;
    var selectedTeeTimes = newTeeTimes[e.target.dataset.indexSchedule];
    selectedTeeTimes.splice(indexTime, 1);
    this.setState({ teeTimes: newTeeTimes });
  },
  generateTeeTime: function generateTeeTime(e) {
    var newTeeTimes = this.state.teeTimes;
    var newTeeTimesArray = [];

    //inset the first one
    var currentTime = new Date();
    var endTime = new Date();
    var typicalCourseDuration = 0.0;

    //setup the time
    currentTime.setHours(e.target.dataset.startTime.split(":")[0]);
    currentTime.setMinutes(e.target.dataset.startTime.split(":")[1]);
    currentTime.setSeconds(0);
    endTime.setHours(e.target.dataset.endTime.split(":")[0]);
    endTime.setMinutes(e.target.dataset.endTime.split(":")[1]);
    endTime.setSeconds(0);

    //2nd tee time
    var secondTeeTime = new Date(currentTime.getTime() + 1000 * 60 * 60 * parseFloat(e.target.dataset.typicalDuration) + 1000 * 60 * parseInt(e.target.dataset.interval));

    //sanity check: ensure that currentTime + typicalCourseDuration > endTime
    var projectedEndTime = new Date(currentTime.getTime() + 1000 * 60 * 60 * e.target.dataset.typicalDuration);
    if (projectedEndTime.getTime() < endTime.getTime()) {
      $.snackbar({ content: 'Projected time to complete course is less than the last flight time', style: 'error' });
      return;
    }

    //sanity check: startTime must be before endTime
    if (currentTime.getTime() > endTime.getTime()) {
      $.snackbar({ content: 'Start Time must be earlier than End Time', style: 'error' });
      return;
    };

    //sanityf check: start and end times must be valid between 0500 and 2100
    if (currentTime.getHours() < 5 || endTime.getHours < 5) {
      $.snackbar({ content: 'Time must set at least 5:00am', style: 'error' });
    };
    if (currentTime.getHours() > 21 || endTime.getHours > 21) {
      $.snackbar({ content: 'Time must set before 9:00pm', style: 'error' });
    };

    //push the times into array
    while (currentTime.getTime() <= endTime.getTime()) {
      //newTeeTimesArray.push([currentTime.toTimeString().substring(0,5), 0 ]);
      newTeeTimesArray.push({ tee_time: currentTime.toTimeString().substring(0, 5),
        second_tee_time: secondTeeTime.toTimeString().substring(0, 5),
        flight_order: 0 });

      currentTime = new Date(currentTime.getTime() + 1000 * 60 * parseInt(e.target.dataset.interval));
      secondTeeTime = new Date(secondTeeTime.getTime() + 1000 * 60 * parseInt(e.target.dataset.interval));
    }

    newTeeTimes[e.target.dataset.indexSchedule] = newTeeTimesArray;
    this.setState({ teeTimes: newTeeTimes });
  },
  render: function render() {
    var _this5 = this;

    return React.createElement(
      'div',
      { className: 'mb-2', id: 'flight', role: 'tabpanel' },
      React.createElement(
        'div',
        { className: 'card' },
        React.createElement(
          'h3',
          { className: 'card-header' },
          'Flight Schedules and Pricing'
        ),
        React.createElement(
          'div',
          { className: 'card-block' },
          'Warning: remove flight schedules with caution'
        ),
        React.createElement(
          'div',
          { className: 'card-block' },
          React.createElement(TaxationBox, { tax_schedule_path: this.props.tax_schedule_path })
        ),
        React.createElement(
          'div',
          { className: 'card-block' },
          this.state.flightSchedules.map(function (e, i) {
            return React.createElement(FlightSchedulePriceCard, { key: i, scheduleIndex: i,
              flightSchedule: e,
              teeTimes: _this5.state.teeTimes[i], deleteTeeTime: _this5.deleteTeeTime, addTeeTime: _this5.addTeeTime,
              generateTeeTime: _this5.generateTeeTime,
              handleClose: _this5.handleClose, updateFlightInfo: _this5.updateFlightInfo, insurance_modes: _this5.props.insurance_modes,
              deleteSchedule: _this5.deleteSchedule
            });
          }),
          React.createElement(
            'button',
            { type: 'button', onClick: this.newSchedule, className: 'btn btn-primary' },
            React.createElement('i', { className: 'fa fa-plus' })
          )
        )
      )
    );
  }
});

